import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
// import mystates from './states.json';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import CircularProgress from '@material-ui/core/CircularProgress';
// import Input from '@material-ui/core/Input';
// import SearchIcon from '@material-ui/icons/Search';
// import InputAdornment from '@material-ui/core/InputAdornment';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import InsertChartIcon from '@material-ui/icons/InsertChart';

import './states.css';



const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0,
    },
    column: {
        flexBasis: '33.33%',
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },

    margin_r: {
        marginRight: '10px',
    }
}));


function Ustates() {

    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
    const [loading, setLoading] = useState(true);


    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const [states, setStates] = useState([]);
    const getAllStates = () => {
        setLoading(true);
        fetch('https://disease.sh/v3/covid-19/states')
            .then(res => res.json())
            .then(data => {
                setStates(data);
                setLoading(false);
            });
    }



    useEffect(() => {
        getAllStates();
    }, []);

    // console.log(states);
    // const sorted = states.map(x => x.state).sort((a, b) => a.localeCompare(b));
    // console.log(sorted);
    if (loading) {
        return (
            <div className="container">
                <div style={{ display: 'flex', justifyContent: 'center', marginTop: '30vh' }}>
                    <CircularProgress />
                </div>
            </div>)
    };
    return (
        <>
            <div className="container">
                <h1>States sorted by number of cases</h1>


                <div className="stats">
                    {
                        states
                            .map(x =>
                                <ExpansionPanel expanded={expanded === x.state} onChange={handleChange(x.state)} key={x.state}>
                                    <ExpansionPanelSummary
                                        expandIcon={<ExpandMoreIcon />}
                                        aria-controls="panel1bh-content"
                                        id="panel1bh-header"
                                    >
                                        <div className={classes.column}>
                                            <Typography className={classes.heading}>{x.state}</Typography>
                                        </div>
                                    </ExpansionPanelSummary>

                                    <ExpansionPanelDetails>
                                        <Typography style={{ fontWeight: '300' }}>
                                            Cases: {x.cases.toLocaleString()} <span>|</span> Active: {x.active.toLocaleString()} <span>|</span> Deaths: {x.deaths.toLocaleString()} <span>|</span> Deaths per Million: {x.deathsPerOneMillion.toLocaleString()} <span>|</span>
                            Today's Cases: {x.todayCases.toLocaleString()} <span>|</span> Today's Deaths: {x.todayDeaths.toLocaleString()} <span>|</span> Tests per million: {x.testsPerOneMillion.toLocaleString()}
                                        </Typography>
                                    </ExpansionPanelDetails>
                                    <Divider />
                                    <ExpansionPanelActions>
                                        <Link to={'/statedetails/' + x.state} style={{ textDecoration: 'none' }}>
                                            <Button size="small" variant="contained" color="primary" className={classes.button}
                                                endIcon={<InsertChartIcon />}>Chart
                                    </Button>
                                        </Link>
                                    </ExpansionPanelActions>
                                </ExpansionPanel>
                            )
                    }
                </div>
            </div>
        </>
    )
}

export default Ustates
// https://disease.sh/v3/covid-19/states
// https://disease.sh/v3/covid-19/nyt/states/texas