import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Bar } from 'react-chartjs-2';
import { useParams } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

function Statechart() {
    const [graphCases, setgraphCases] = useState([]);
    const [graphDeaths, setgraphDeaths] = useState([]);
    const [respdates, setDates] = useState([]);
    // const [state, setState] = useState();
    const stateParam = useParams().state;
    const [loading, setLoading] = useState(true);
    const [dataError, setDataError] = useState('');
    const respCases = [];
    const respDeaths = [];
    const respDates = [];


    const fetchStateGraphs = (stateParam) => {
        setLoading(true);
        fetch(`https://disease.sh/v3/covid-19/nyt/states/${stateParam}`)
            .then(res => res.json())
            .then((r) => {
                r.forEach(r =>
                    respCases.push(r.cases));
                r.forEach(r =>
                    respDeaths.push(r.deaths));
                r.forEach(r => respDates.push(r.date));

                setgraphCases(respCases);
                setgraphDeaths(respDeaths);
                setDates(respDates);

                // setState(stateParam);
                setLoading(false);
            }).catch(function () {
                setDataError('No data available.');
            });
    }

    useEffect(() => {
        fetchStateGraphs(stateParam);
    }, [stateParam]);


    const dates = (respdates);
    const casenum = (graphCases);
    const deathsnum = (graphDeaths);
    const data = {
        labels: dates,
        datasets: [
            {
                label: "Cases",
                data: casenum,
                borderColor: "rgba(33, 150, 243, 1)",
                borderWidth: 1,
                backgroundColor: "rgba(33, 150, 243, 0.6)",

            },

            {
                label: "Deaths",
                data: deathsnum,
                borderColor: "rgba(255, 87, 34, 1)",
                borderWidth: 1,
                backgroundColor: "rgba(255, 87, 34, 0.6)"
            }

        ]
    }

    const container = {
        minHeight: '80vh',
        width: '100vw',
        display: 'flex',
        flexWrap: 'wrap',
        alignContent: 'center',
        justifyContent: 'center'
    }

    const graphBox = {
        width: '90%',
        marginLeft: 'auto',
        marginRight: 'auto',
        marginTop: '30px'
    }

    if (dataError) {
        return (
            <p style={container}>{dataError}</p>
        )
    }

    if (loading) {
        return (
            <div style={container}>
                <div style={{ display: 'flex', justifyContent: 'center', marginTop: '30vh' }}>
                    <CircularProgress />
                </div>
            </div>)
    };

    return (
        <div style={container}>
            <Link to={"/states"}>
                <div style={{ alignSelf: 'flex-start' }}>
                    <ArrowBackIosIcon fontSize="small" />
                </div>
            </Link>
            <div>
                {<Typography variant="h5" style={{ textAlign: 'center' }}>Graph for {stateParam} </Typography>}
                {<Typography style={{ textAlign: 'center' }}>Click the legend below to isolate records</Typography>}
            </div>
            <div style={graphBox}>
                <Bar data={data} />
            </div>
        </div>
    )
}

export default Statechart
